@tailwind base;
@tailwind components;
@tailwind utilities;

@media (max-width: 720px) {
    .heightonmobile {
        top: 10%; /* Move arrows up for smaller screens */
    }
  
    .footer {
      padding: 10px 20px;
      font-size: 0.9em;
    }
    
    .footer-text {
      text-align: center;
      margin-top: 10px;
      white-space: normal;
    }
  }

  @media (max-width: 640px) {
    footer p {
      margin-top: .25rem; /* Adjust as needed to add space */
      text-align: center; /* Center-align for a cleaner look */
    }
  }

